@import "../../node_modules/bootstrap/scss/bootstrap-reboot.scss";
@import "../../node_modules/bootstrap/scss/bootstrap-grid.scss";

$accent-color: #0085FF;
$font-color: #121212;

@font-face {
    font-family: Gilroy-Regular;
    src: url('../fonts/Gilroy-Regular.ttf'); }

@font-face {
    font-family: Gilroy-Bold;
    src: url('../fonts/Gilroy-Bold.ttf'); }

@font-face {
    font-family: Gilroy-SemiBold;
    src: url('../fonts/Gilroy-SemiBold.ttf'); }

* {
    box-sizing: border-box; }


body {
    font-family: Gilroy-Regular;
    font-size: 18px;
    color: $font-color; }

ul {
    padding: 0;
    margin: 0;
    list-style: none; }

img {
    &:hover, &:active, &:focus {
        outline: 0; } }

.accent_btn {
    line-height: 0;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    font-family: Gilroy-Bold;
    background: $accent-color;
    color: #fff;
    width: 170px;
    height: 40px;
    transition: .3s;
    &:hover, &:active, &:focus {
        outline: 0;
        background: lighten($accent-color, 10%); } }

.section_title {
    font-family: Gilroy-Bold;
    font-weight: normal;
    font-size: 48px;
    line-height: 105.5%;
    margin: 80px 0 70px; }

.menu {
    padding: 0 60px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f3f3f3; }

.menu_part {
    display: flex;
    align-items: center; }

.logo {
    height: 42px;
    width: auto; }

.menu_desc {
    margin: 0 0 0 25px;
    font-size: 16px;
    color: #cfcfcf; }

.menu_items {
    display: flex;
    align-items: center;
    li>a {
        display: block;
        margin-right: 40px;
        padding: 34px 0;
        font-size: 16px;
        color: #a3a3a3;
        position: relative;
        transition: 0.3s;
        &:before {
            content: '';
            position: absolute;
            height: 1px;
            width: 100%;
            background-color: $font-color;
            bottom: -1px;
            left: 0;
            opacity: 0;
            transition: 0.3s; } }
    li>a:hover, li>a:active, li>a:focus, a.active_link {
        outline: 0;
        text-decoration: none;
        color: $font-color;
        &:before {
            opacity: 1; } } }

.footer {
    padding: 50px 0 25px;
    background: #090909; }

.footer_menu {
    display: flex;
    align-items: center;
    padding-top: 9px;
    margin-bottom: 65px;
    li>a {
        font-family: Gilroy-SemiBold;
        font-weight: normal;
        font-size: 18px;
        color: #fff;
        margin-right: 40px;
        transition: .3s;
        &:hover, &:active, &:focus {
            color: $accent-color;
            text-decoration: none; } } }

.footer_inst {
    padding-top: 9px; }

.inst_link {
    display: flex;
    justify-content: space-between;
    text-decoration: underline;
    color: #fff;
    font-family: Gilroy-SemiBold;
    font-weight: normal;
    transition: .3s;
    &:hover, &:active, &:focus {
        color: $accent-color;
        .inst_icon {
            fill: $accent-color; } } }

.inst_icon {
    width: 30px;
    height: 30px;
    fill: #fff;
    transition: .3s; }

.footer_contacts {
    display: flex;
    align-items: center;
    margin-bottom: 45px; }

.footer_contact {
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 60px;
    margin-right: 30px;
    overflow: hidden;
    a {
        color: #fff;
        font-size: 16px;
        padding: 12px 30px;
        display: block;
        transition: .3s;
        &:hover, &:active, &:focus {
            color: #fff;
            text-decoration: none;
            background: $accent-color; } } }

.contact_icon {
    fill: #fff;
    width: 20px;
    height: 20px;
    margin-right: 10px; }

.copyright, .copyright_link {
    font-size: 14px;
    color: rgba(255, 255, 255, .3);
    opacity: 0.3; }

.copyright_link {
    text-decoration: underline;
    transition: .3s;
    &:hover, &:active, &:focus {
        color: #fff; } }


@import "includes/index";
@import "includes/portfolio";
@import "includes/services";
@import "includes/contacts";
@import "includes/about";
@import "includes/project";
@import "includes/service_page";
