.srv_img_cont_1 {
    width: 100%;
    height: 240px;
    background: #384465;
    position: relative;
    margin-bottom: 155px; }

.srv_img_1 {
    position: absolute;
    bottom: -75px;
    left: 50%;
    transform: translateX(-50%);
    width: 470px;
    height: auto; }

.srv_title {
    font-family: Gilroy-Bold;
    font-weight: normal;
    font-size: 38px;
    line-height: 110%;
    text-align: center;
    margin-bottom: 27px; }

.srv_tags {
    font-size: 16px;
    line-height: 190%;
    color: #A3A3A3;
    text-align: center;
    margin-bottom: 100px;
    span {
        margin-left: 44px;
        position: relative;
        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: -24px;
            transform: translateY(-50%);
            width: 4px;
            height: 4px;
            background: #C4C4C4;
            border-radius: 50%; } } }

.srv_txt_title {
    font-size: 28px;
    line-height: 110%;
    font-weight: normal;
    font-family: Gilroy-Bold;
    margin-bottom: 30px; }

.srv_txt_block {
    margin-bottom: 90px;
    p {
        margin-bottom: 30px; } }


.ul_point {
    margin-bottom: 30px;
    li {
        padding-left: 28px;
        position: relative;
        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            width: 8px;
            height: 8px;
            background: $accent-color;
            border-radius: 50%; } } }

.srv_list_title {
    font-weight: normal;
    font-family: Gilroy-Bold;
    font-size: 22px;
    line-height: 110%;
    margin-bottom: 30px;
    padding-top: 30px;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: 0px;
        left: 0;
        width: 130px;
        height: 4px;
        background: $accent-color; } }

.srv_right_list {
    li {
        position: relative;
        padding-left: 30px;
        margin-bottom: 15px;
        &:before {
            content: '';
            position: absolute;
            top: 11px;
            left: 0;
            width: 15px;
            height: 1px;
            background: $accent-color; } } }

.srv_num {
    font-family: Gilroy-Bold; }

.srv_list_link {
    color: $accent-color;
    transition: .3s;
    &:hover, &:active, &:focus {
        color: lighten($accent-color, 10%);
        text-decoration: none; } }

.svr_accent_txt {
    font-size: 20px;
    line-height: 28px;
    font-family: Gilroy-SemiBold; }
