.services_section {
    padding: 130px 0 170px;
    .page_desc {
        margin-bottom: 75px; } }

.service {
    padding: 65px 40px 40px 70px;
    margin-bottom: 30px;
    min-height: 470px;
    display: block;
    color: $font-color;
    &:hover, &:active, &:focus {
        text-decoration: none;
        color: inherit; } }

.service_title {
    font-size: 28px;
    line-height: 105.5%;
    font-family: Gilroy-SemiBold;
    margin-bottom: 20px; }

.service_desc {
    font-size: 18px;
    line-height: 140%;
    color: #696969;
    max-width: 385px;
    margin: 0; }

.service_1 {
    background: #f5f5f5;
    .service_img {
        margin-top: 35px; } }

.service_2 {
    border: 6px solid #F5F5F5;
    padding: 59px 34px 34px 64px;
    .service_desc {
        max-width: 265px; }
    .service_img {
        margin-top: 60px; } }

.service_3 {
    background: #E1EFC2;
    .service_img {
        margin-top: 48px; } }

.service_4 {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-right: 70px;
    min-height: 420px;
    .service_img {
        margin: 0; } }

.service_5 {
    .service_img {
        margin-top: 50px; }
    .service_desc {
        max-width: 400px; } }

.more_services_container {
    display: flex;
    justify-content: center;
    align-items: center; }

.page_desc a {
    text-decoration: underline;
    color: $accent-color;
    transition: .3s;
    &:hover, &:active, &:focus {
        color: lighten($accent-color, 10%); } }
